import 'simplelightbox/dist/simple-lightbox.min.css';

const gallery = document.querySelector('.gallery');
let lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});

// webformatURL â€” Ð¿Ð¾ÑÐ¸Ð»Ð°Ð½Ð½Ñ Ð½Ð° Ð¼Ð°Ð»ÐµÐ½ÑŒÐºÐµ Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð½Ñ Ð´Ð»Ñ ÑÐ¿Ð¸ÑÐºÑƒ ÐºÐ°Ñ€Ñ‚Ð¾Ðº Ñƒ Ð³Ð°Ð»ÐµÑ€ÐµÑ—
// largeImageURL â€” Ð¿Ð¾ÑÐ¸Ð»Ð°Ð½Ð½Ñ Ð½Ð° Ð²ÐµÐ»Ð¸ÐºÐµ Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð½Ñ Ð´Ð»Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð°
// tags â€” Ñ€ÑÐ´Ð¾Ðº Ð· Ð¾Ð¿Ð¸ÑÐ¾Ð¼ Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð½Ñ. ÐŸÑ–Ð´Ñ–Ð¹Ð´Ðµ Ð´Ð»Ñ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° alt
// likes â€” ÐºÑ–Ð»ÑŒÐºÑ–ÑÑ‚ÑŒ Ð²Ð¿Ð¾Ð´Ð¾Ð±Ð°Ð¹Ð¾Ðº
// views â€” ÐºÑ–Ð»ÑŒÐºÑ–ÑÑ‚ÑŒ Ð¿ÐµÑ€ÐµÐ³Ð»ÑÐ´Ñ–Ð²
// comments â€” ÐºÑ–Ð»ÑŒÐºÑ–ÑÑ‚ÑŒ ÐºÐ¾Ð¼ÐµÐ½Ñ‚Ð°Ñ€Ñ–Ð²
// downloads â€” ÐºÑ–Ð»ÑŒÐºÑ–ÑÑ‚ÑŒ Ð·Ð°Ð²Ð°Ð½Ñ‚Ð°Ð¶ÐµÐ½ÑŒ

export function createGallery(images) {
  const markup = images
    .map(
      ({
        webformatURL,
        largeImageURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => `
      <li class="gallery-item">
        <a class="gallery-link" href="${largeImageURL}">
          <img class="gallery-image" src="${webformatURL}" alt="${tags}" />
        </a>
        <div class="info">
         <div class="info-block">
          <p clas="label" >Likes: </p>
          <p clas="value" >${likes}</p> </div>
           <div class="info-block">
          <p clas="label">Views:</p>
          <p clas="value" >${views}</p> </div>
           <div class="info-block">
          <p>Comments:</p>
          <p clas="value" >${comments}</p> </div>
           <div class="info-block">
          <p clas="label">Downloads:</p>
          <p clas="value" >${downloads}</p></div>
        </div>
      </li>`
    )
    .join('');

  gallery.innerHTML = markup;
  lightbox.refresh();
  //    Ð¼ÐµÑ‚Ð¾Ð´ Ð±Ñ–Ð±Ð»Ñ–Ð¾Ñ‚ÐµÐºÐ¸, ÑÐºÐ¸Ð¹ Ð¾Ð±Ð¾Ð²'ÑÐ·ÐºÐ¾Ð²Ð¾ Ð¿Ð¾Ñ‚Ñ€Ñ–Ð±Ð½Ð¾ Ð²Ð¸ÐºÐ»Ð¸ÐºÐ°Ñ‚Ð¸ Ñ‰Ð¾Ñ€Ð°Ð·Ñƒ Ð¿Ñ–ÑÐ»Ñ Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ Ð½Ð¾Ð²Ð¸Ñ… ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ–Ð² Ð´Ð¾ Ð³Ð°Ð»ÐµÑ€ÐµÑ—.
}

export function clearGallery() {
  gallery.innerHTML = ' ';
}

export function showLoader() {
  document.getElementById('loader').classList.remove('is-hidden');
}

export function hideLoader() {
  document.getElementById('loader').classList.add('is-hidden');
}
